.brand {
    position: relative;
    z-index: 999;

    img {
        width: 75px;
    }
}




.user {
    .avatar {
        img {
            height: 28px;
            width: 28px;
        }
    }

    &:hover {
        border-bottom: 1px solid transparent !important;
    }

    &+.dropdown-menu {
        left: initial;
        right: 0;
        text-align: right;
        font-size: 14px;

        .name {
            display: block;
            width: 100%;
            padding: .25rem 1.5rem;
        }

        @include media('<tablet') {

            max-width: 300px;
            margin: auto;
            text-align: center;

        }
    }
}


.map {

    img {
        width: 300px;
        height: 300px;
        object-fit: cover;
    }

    &-link {
        position: absolute;
        top: 20px;
        left: 20px;
        background: $white;
        padding: 3px 10px;
    }
}

.badge {
    padding: 5px 7px;
    color: $white;
    font-weight: normal;

    &-settled {
        background: $settled;
    }

    &-rejected {
        background: $rejected;
    }

    &-draft {
        background: $draft;
    }

    &-submitted {
        background: $submitted;
    }

    &-processing {
        background: $processing;
    }
}

.page-item.active {
    .page-link {
        background-color: $brand;
        border-color: $brand;
    }
}