.claim-summary {
    border:1px solid $brdcolor;
    &-item {
        list-style: none;
        border-right:1px solid $brdcolor;
        @include media('>desktop'){
            width: 20%;
        }
        
        &:last-child {
            border-right:none;
        }
    }
    &-label {
        font-size: 14px;
    }
    .total-claim {
        background: #F1F1F1;
        @include media('<tablet'){
            width: 100%;
        }
    }
    .settled {
        .claim-summary-number {
            color:$settled;
        }
    }
    .rejected {
        .claim-summary-number {
            color:$rejected;
        }
    }
    .draft {
        .claim-summary-number {
            color:$draft;
        }
    }
    .submitted {
        .claim-summary-number {
            color:$submitted;
        }
    }
}