.list {
    border: 1px solid $brdcolor;
    border-radius: 3px;

    &-item {
        position: relative;
        font-size: 15px;
        display: block;
        padding: .75rem 0.25rem;
        color: $black;
        border-bottom: 1px solid $brdcolor;

        &:after {
            content: "";
            background: url(../img/arrow-r.svg) no-repeat 0 0px;
            width: 20px;
            height: 15px;
            display: block;
            position: absolute;
            right: 0;
            top: 18px;
            opacity: 0;
            transition: all 0.3s ease;
        }

        &:hover {
            text-decoration: none;
            color: $brand;

            &:after {
                opacity: 1;
                background-position: 100% 0px;
                transition: all 0.3s ease;
            }
        }

        &.active {
            color: $brand;
            font-weight: bold;

            &:after {
                opacity: 1;
                background-position: 100% 0px;
            }
        }

        &:last-child {
            border-bottom: none;
        }
    }

    &.noarrow {
        .list-item {
            &:after {
                content: "";
                display: none;
            }
        }
    }

}

.list-style {
    &-a {
        li {
            list-style: none;

            a {
                color: $black;
                position: relative;
                padding-left: 18px;
                line-height: 1.9;

                &:before {
                    content: "";
                    height: 5px;
                    width: 5px;
                    background: $grayblue;
                    position: absolute;
                    top: 10px;
                    left: 0;
                    border-radius: 50%;
                }
            }
        }
    }
}

.list-group-item {
    font-size: 15px;

    &.active {
        background: $brand;
        border-color: $brand;
    }
}

// category list
.category {
    &-item {
        display:inline-block;
        color: $black;
        border:1px solid $brdcolor;
        padding: 2px 5px; margin-bottom: 3px;
    }
}