.accordion {
    .card {
        border-bottom: 1px solid theme(border,dark) !important;
        &:last-child {
            border-bottom: none !important;
        }
    }
    .card-header {
        padding:0;
        border:none;
        cursor: pointer;
        &-item {
            border-bottom: 1px solid $white;
            padding:1rem 0.5rem 0.1rem;
            font-size: 16px;
            color:$brand;
            font-weight: 400;
            .accordion-indicator{
                margin-top: -8px;
                text-align: center;
                width: 20px;
                i.fa-plus {
                    display: none;
                    font-size: 28px;
                }
                i.fa-minus{
                    display: block;
                    font-size: 28px;
                }
        
            }   
                  
        }
        &-item.collapsed {
            color:$black;
            border-bottom: 1px solid rgba(0,0,0,.125);
            .accordion-indicator{
                margin-top: -8px;
                text-align: center;
                width: 20px;
                i.fa-plus {
                    display: block;
                    font-size: 28px;
                }
                i.fa-minus{
                    display: none;
                    font-size: 28px;
                }
        
            }
        }
        &-item {
            &:hover {
                color:$brand;
            }   
        }
    }
    .card-body {
        padding:0rem 2rem 2rem;
        margin-top: -0.1rem;
    }
    
    
}