.msg {
    &-box {
        border: 1px solid $brdcolor;
        overflow: hidden;
    }

    // list
    &-list {
        background: #F4F4F4;
        border-right: 1px solid $brdcolor;

        &-item {
            list-style-type: none;
            transition: all 0.3s ease;
            cursor: pointer;

            &:hover {
                background: $white;
                color: $brand;
                position: relative;
                z-index: 1;
                box-shadow: 0 0 5px rgba($black, 0.1);
            }

            &.active {
                background: $white;
                color: $brand;
                font-weight: 500;
                position: relative;
                z-index: 1;
                box-shadow: 0 0 5px rgba($black, 0.1);

                &:after {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0px;
                    width: 5px;
                    height: 100%;
                    background: $brand;
                }
            }

            &.date {
                cursor: default;

                &:hover {
                    background: transparent;
                    box-shadow: none;
                    color: $black;

                }

                .label {
                    font-weight: bold;
                    font-size: 12px;
                }
            }

        }
    }

    &-unread {
        background: #E7E5F3;

        &:hover {
            background: #E7E5F3;
            color: $black;
            position: relative;
            z-index: 1;
            box-shadow: 0 0 5px rgba($black, 0.2);
        }
    }

    // details
    &-view {
        &-empty {
            background: url(../img/message.svg) no-repeat center center;
            background-size: 350px;
            height: 100%;
            width: 100%;
        }
    }

    &-attachment * {
        margin-right: 5px;
    }

    &-attachment-file {
        background: #ECECEC;
        font-size: 13px;
        display: inline-block;
        margin-bottom: 0.3rem;
    }


    // old
    &-old {
        .msg-detail {
            .msg-action {
                display: none;

            }
        }

        .msg-detail:hover {
            .msg-action {
                display: block;
            }
        }
    }

    // reply
    &-reply {
        background: #F0F0F0;

        .form-control {
            border: none;
        }
    }


}

.uploaded-files {
    .msg-attachment-file {
        border: 1px solid #707070;
    }

    .close {
        font-size: 1.2rem;
    }
}

@media only screen and (min-width: 992px) {
    .msg-view {
        padding: 24px;
    }

    .goto-msg-list {
        display: none;
    }
}

@media only screen and (max-width: 991px) {
    .msg-list {
        transform: translateX(0);
        height: 100%;
        transition: all 0.3s ease;
    }

    .msg-view {
        transform: translateX(100%);
        transition: all 0.3s ease;
        height: 0;
        padding: 0;
    }

    .mobile {
        .msg-list {
            transform: translateX(-100%);
            height: 0;
            transition: all 0.3s ease;
        }

        .msg-view {
            transform: translateX(0);
            transition: all 0.3s ease;
            height: 100%;
            padding: 24px;
        }

        .goto-msg-list {
            display: inline-block;
        }

    }

}




.slimScrollBar {
    opacity: 0.2 !important;
}




.dropdown-md {
    width: 300px;
}

.notification-msg {

    a,
    p {
        font-size: 13px;
        color: $black;
        padding: 0.5rem;
        margin-bottom: 0;

        &:hover {
            text-decoration: none;
            background: #f2f2f2;
        }
    }
    .media {
        border-top:1px solid $brdcolor;
    }

    .dropdown-toggle{
        &::after {
            display: none;
        }
        i {
            font-size: 20px;
            margin-top: 3px;
        }
        &:hover {
            border:none !important;
            background: none;
        }
    }
    .unread {
        a,p {
            background: #E7E5F3;
        }
    }

}

.notification-foot {
    padding: 8px 16px;
    border-top: 1px solid #eee;
}
.notification-head {
    padding: 8px 16px;
}

.notification-badge {
    position: absolute;
    top: 0px;
    right: 5px;
    min-width: 14px;
    max-width: 50px;
    font-size: 11px;
    background: red;
    color: #fff;
    display: block;
    text-align: center;
    padding: 1px 3px;
    border-radius:4px;
    }