.fluid-layout {
    .masthead {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9999;
        margin: 0 auto;
        width: 100%;
        transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);

        .navbar {
            transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);

            a.btn {
                border-color: $white;
                color: $white;
                padding: .375rem .75rem;

                &:hover {
                    border-color: $brand;
                }
            }

            .nav-link {
                color: $white;
                border-bottom: 1px solid transparent;

                &:hover {
                    border-bottom: 1px solid $white;
                }
            }
        }

    }



    .sticky {
        background: $white;
        box-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
        margin-top: 0;

        .navbar {

            a.btn {
                border-color: $brand;
                color: $brand;

                &:hover {
                    border-color: $brand;
                    color: $white;
                }
            }

            .nav-link {
                color: $black;

                &:hover {
                    border-color: $brand;
                }
            }

        }



    }

    .small-hd {
        .navbar {
            padding-top: 0.4rem !important;
            padding-bottom: 0.4rem !important;
        }

        // .navbar-collapse {
        //     @include media('<tablet') {
        //         height: calc(100vh - 64px);
        //         top: 64px;
        //     }
        // }

    }

    .container .jumbotron {
        @include media('<tablet') {
            margin-left: -15px;
            margin-right: -15px;
        }
    }
}

.footer {
    >div.py-5 {
        width: 100%;
        max-width: 1340px;
        padding-left: 15px;
        padding-right: 15px;
        margin: 0 auto;
    }
}

.main-promo {
    height: 100vh;
    max-height: initial;
    min-height: initial;
    padding-top: 10rem !important;
    background-attachment: fixed;

    @include media('<tablet') {
        padding: 5rem 20px 6rem 20px !important;
        height: 100%;

        .heading {
            font-size: 2.65rem;
        }
    }
}

.inner-pg {
    .masthead {
        background: $white;
        .navbar {

            a.btn {
                border-color: $brand;
                color: $brand;
                padding: .375rem .75rem;

                &:hover {
                    color:$white;
                }
            }

            .nav-link {
                color: $black;
                border-bottom: 1px solid transparent;

                &:hover {
                    border-bottom: 1px solid $black;
                }
            }
        }
        .navbar-toggler {
            span {
                background: $black;
            }
        }

    }
}
