.slick-arrow {
    display:none !important;
    transition: all o.3s ease;
}
.responsive-carousel {
    .card {
        margin:0 10px 20px;
        border:none;
        border-radius: 0;
        a {
            color: $black;
        }
        &-body {
            @include media('>tablet') {
                min-height: 132px;
                max-height: 145px;
            }
        }
    }
    &:hover {
        .slick-arrow {
            display:block !important;
        }
    }
}

