.number-bullet {
    height: 48px;
    width: 48px;
    border:1px solid $brand; 
    color:$brand;
}

.nav-pills {
    .nav-link{
        cursor: pointer;
        &:hover {
            background: #f0f8ff;
        }
        &.active {
            background: $white;
            color:$black;
            box-shadow: 0 2px 12px rgba(0,0,0,0.2);
            .number-bullet {
                background: $brand;
                color:$white;
            }
        }
    }
    h4 {
        font-size: 1.5rem;
    }
}

