.btn {
    padding: .3rem 2rem;
}
.btn-rounded {
    border-radius: 3rem;
}

.main-promo {
    .btn-outline-default {
        border-color: $white;
        color: $white;
        &:hover {
            background: $white;
            color:$black;
        }
    }
}

.btn-primary {
    background-color: $brand;
    border-color: $brand;

    &:hover {
        background-color: darken($brand, 5);
        border-color: darken($brand, 5);
    }
}

.btn-outline-primary {
    color: $brand;
    border-color: $brand;
    &:hover {
        background-color: $brand;
        border-color: $brand;
    }
}

.btn-link {
    color: $brand;

    &:hover {
        color: $brand;
    }
}

.btn-outline-primary:not(:disabled):not(.disabled).active,
.btn-outline-primary:not(:disabled):not(.disabled):active,
.show>.btn-outline-primary.dropdown-toggle {
    background-color: $brand;
    border-color: $brand;
}