
.navbar-collapse {
    @include media('<tablet') {
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 0;
        left: -100%;
        padding-top: 100px;
        background: $white;
        text-align: center;

        &.show {
            left:0;
        }

        .nav-link {
            color: $black !important;
            font-size: 20px;
            opacity: 0.7;
        }

        a.btn {
            border-color: $brand !important;
            color: $brand !important;
            margin-right: 0 !important;
            font-size: 20px !important;
            margin-bottom: 10px;

            &:hover {
                color: $white !important;
            }
        }

    }
}

.navbar-toggler {
    position: relative;
    z-index: 999;
    height: 24px;
    span {
        background: $white;
        height: 1px;
        width: 100%; 
        position: absolute;
        left: 0;
        transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
        &.one {
            top:0;
        }       
        &.two {
            top: 50;
        }
        &.three {
            bottom:0;
        }
    }
    &.collapsed {
        color: $white;
    }
    &[aria-expanded="true"] {
        span {
            background: $black;
            &.one {
                top:11px;
                transform: rotate(45deg);
            }       
            &.two {
                opacity: 0;
            }
            &.three {
                bottom:10px;
                transform: rotate(-45deg);
            }
        }
    }
}

.claim-image-container{
    height:140px;
    width:100%;
    & img{
        width:100%;
        height:100%;
        object-fit:cover;
    }
}

.header-border .masthead {
    border-bottom: 1px solid #ebebeb !important;
}