.main-promo {
    background: url(../img/banner-image.jpg) no-repeat center top /cover;
    height: calc(95vh - 74px);
    max-height: 720px;
    min-height: 580px;
    position: relative;


    &-caption {
        color: $white;

        &.subheading {
            @include media('>tablet') {
                max-width: 528px;
            }
        }
    }

    &:after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        background: rgba($black, 0.2);

        @include media('>tablet') {
            display: none;
        }
    }
}

.arrow-yholder {
    height: 50px;
    width: 18px;
    position: absolute;
    bottom: 4rem;
    left: 50%;
    margin-left: -9px;
}

.arrow-y {
    position: absolute;
    -webkit-animation-name: arrow-ymovement;
    /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 0.9s;
    /* Safari 4.0 - 8.0 */
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
    animation-name: arrow-ymovement;
    animation-duration: 0.9s;
    animation-iteration-count: infinite;
    animation-direction: alternate;

    img {
        width: 18px;
    }
}

.secondary-promo {
    background: url(../img/success-stories.jpg) no-repeat center top /cover;
    min-height: 440px;
}

.getstarted-promo {
    background: url(../img/getstarted.jpg) no-repeat center top /cover;
    min-height: 255px;
}

.inner-promo {
    background: url(../img/img1.jpg) no-repeat center top /cover;
    // height: 280px;
    position: relative;
    .main-promo-caption {
        color: #5c5c5c;
    }

    &:after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        background: rgba($white, 0.5);
    }

    // @include media('>desktop') {
    //     height: 180px;
    // }
}