.m {

    // top
    &t-8 {
        margin-top: $s8;
    }
    &t-7 {
        margin-top: $s7;
    }
    &t-6 {
        margin-top: $s6;
    }

    // bottom
    &b-8 {
        margin-bottom: $s8;
    }
    &b-7 {
        margin-bottom: $s7;
    }
    &b-6 {
        margin-bottom: $s6;
    }
}
.p {

    // top
    &t-8 {
        padding-top: $s8;
    }
    &t-7 {
        padding-top: $s7;
    }
    &t-6 {
        padding-top: $s6;
    }

    // bottom
    &b-8 {
        padding-bottom: $s8;
    }
    &b-7 {
        padding-bottom: $s7;
    }
    &b-6 {
        padding-bottom: $s6;
    }
}

.z-index{
    &-1 {
        z-index: 1;
    }
    &-2 {
        z-index: 2;
    }
    &-3 {
        z-index: 3;
    }
}


// alert messages custom
.alert {
    &-primary.custom {
        background: #E4F7FF;
        border-color: #00B9FF;
    }
}


.avatar {
    &-md {
        height: 80px;
        width: 80px;

    }
}
