.blog {
    &-list-item {

        &:hover {
            .blog-cover-img {
                img {
                    transform: scale(1.03);
                }
            }
        }

    }

    &-cover {
        position: relative;

        &-img {
            height: 300px;
            overflow: hidden;
            display: block;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: all 0.3s ease-in-out;
            }
        }

    }

    &-kicker {
        margin-top: 20px;
        font-size: 13px;

        @include media('>tablet') {
            position: absolute;
            top: 0px;
            left: -10.333333%;
            background: $white;
            padding: 20px;
            max-width: 200px;
            margin-top: 27px;
        }
    }

    &-date {
        font-size: 16px;
    }

    &-title {
        a {
            color: $black;
        }
    }

    &-summary {
        color: lighten($black, 50);
        position: relative;

        @include media('>tablet') {
            max-width: 80%;
        }
    }

    &-social {
        @include media('>tablet') {
            position: absolute;
            left: -10%;
            top: 0;
        }

        a {
            @include media('>tablet') {
                display:block;
            }
            display:inline-block;
            margin-bottom: 10px;
            opacity: 0.7;
            &:hover {
                opacity: 1;
            }
            i {
                font-size: 24px;
            }
        }
    }

    // detail
    &-detail {
        .blog-cover-img {
            height: auto;
            max-height: 450px;
        }

        &:hover {
            .blog-cover-img {
                img {
                    transform: scale(1);
                }
            }
        }
    }
}